import React from "react";
import { useState } from "react";
import Heading from "../Heading/Heading";
import "./MemoryImg.scss";
export default function MemoryImg({ title, sub, id, MemoryImg }) {
     const [idImg, setIdImg] = useState(0);
     const handleNextImg = () => {
          setIdImg(idImg + 1);
          console.log(idImg);
          if (idImg >= MemoryImg.length - 1) {
               setIdImg(0);
          }
          const imgShow = document.querySelector(".me__full--show-item");
          imgShow.setAttribute("src", MemoryImg[idImg].link);
     };
     const handlePrevImg = () => {
          setIdImg(idImg - 1);
          console.log(idImg);
          if (idImg <= 0) {
               setIdImg(MemoryImg.length - 1);
          }
          const imgShow = document.querySelector(".me__full--show-item");
          imgShow.setAttribute("src", MemoryImg[idImg].link);
     };
     const handleImageClose = () => {
          const showContainer = document.querySelector(".me__full--show");
          showContainer.style.display = "none";
     };
     const handleImageClick = (item, index) => {
          const imgShow = document.querySelector(".me__full--show-item");
          const showContainer = document.querySelector(".me__full--show");
          imgShow.setAttribute("src", item.link);
          showContainer.style.display = "flex";
          setIdImg(index);
     };
     return (
          <div id={id} className="me__full">
               <Heading sub={sub} id={id} title={title} />
               <div className="me__container">
                    {MemoryImg.map((item, index) => (
                         <div
                              key={index}
                              onClick={(e) => handleImageClick(item, index)}
                              className="me__box"
                         >
                              <img src={item.link} alt="a" />
                         </div>
                    ))}
               </div>
               <div className="me__full--show">
                    <div
                         onClick={(e) => handleImageClose(e)}
                         className="me__full--overlay"
                    ></div>
                    <button
                         onClick={(e) => handleImageClose(e)}
                         className="me__full--close"
                    >
                         <i className="fas fa-times-circle"></i>
                    </button>

                    <img className="me__full--show-item" src="" alt="a" />
                    <button onClick={handlePrevImg} className="prev__img">
                         <i className="fas fa-chevron-circle-left"></i>
                    </button>
                    <button onClick={handleNextImg} className="next__img">
                         <i className="fas fa-chevron-circle-right"></i>
                    </button>
               </div>
          </div>
     );
}
